import axios from "axios";
import _ from "lodash";
import CryptoJS from "crypto-js";
// import base64 from "react-native-base64";
const instance = axios.create({
  method: "POST",
  baseURL: '/'
});

export const query = async (url, params = {}) => {
  var token = await localStorage.getItem('token');
  if (!token) token = "";
  const timestamp = Math.floor(new Date().getTime() / 1000).toString();
  //const hash = await generateHash(params, url, timestamp);
  const data = formData(params);
  let request = {
    url,
    // headers: {
    //   //authorization: `${token}:${hash}`,
    //   timestamp
    // }
  };
  if (data) request.data = data;
  console.log('CALL ACTION', url, params, data);
  return instance(request).then(response => {
    const {
      data: { json }
    } = response;
    console.log(`response ${url}`, response);
    if(json && json.error === 401){
      localStorage.clear(async () => {
        await localStorage.removeItem('token');
      });
    }

    if (
      json && json.hasOwnProperty("error") &&
      typeof json.error !== "undefined" &&
      json.error.toString() === "-1"
    ) {
     
      // reset();
      throw new Error(json && json.error);
    }
    return response;
  }).catch(error => {
    return error.response
  });
};

const generateHash = async (obj, url, timestamp) => {
  // console.log(obj);
  

  function sortReqrs(obj){
    let params = {};
    let result = {}
    if (typeof obj !== "object") return obj;
    params = _(obj)
      .toPairs()
      .sortBy(0)
      .fromPairs()
      .value();

    Object.keys(params).forEach(key => {
      
      result[key] = sortReqrs(params[key])
    });
    return result
  }


  const sorted = sortReqrs(obj)
  // const sorted = _(params)
  //   .toPairs()
  //   .sortBy(0)
  //   .fromPairs()
  //   .value();

  //console.log(11111, sorted);
  const string = toQueryString(sorted, true);
  //console.log(2222, string);
  const data = toString(url, timestamp, string);
  //console.log(33333, data);
  var hash = await CryptoJS.HmacSHA256(data, config.SECRET_KEY);
  //console.log(44444, hash.toString());
  hash = await CryptoJS.enc.Base64.stringify(hash);
 // console.log(5555, JSON.stringify(obj) + "\n" + hash + "\n" + data);
  //console.log(66666, obj, hash, data);
  return hash;
};

const toString = (url, timestamp, str) => {
  const arr = ['/' + url, "POST", timestamp, str];
  return arr.join("\n");
};

const toQueryString = (obj, urlEncode) => {
  const flattenObj = (x, path) => {
    var result = [];

    path = path || [];
    if (!x) return result;
    Object.keys(x).forEach(key => {
      if (!x.hasOwnProperty(key)) return;

      var newPath = path.slice();
      newPath.push(key);

      var vals = [];
      if (typeof x[key] == "object") {
        vals = flattenObj(x[key], newPath);
      } else {
        vals.push({ path: newPath, val: x[key] });
      }
      vals.forEach(obj => result.push(obj));
    });
    return result;
  };

  var parts = flattenObj(obj).map(varInfo => {
    if (varInfo.path.length === 1) varInfo.path = varInfo.path[0];
    else {
      var first = varInfo.path[0];
      var rest = varInfo.path.slice(1);
      varInfo.path = first + "[" + rest.join("][") + "]";
    }
    return varInfo;
  });
  var queryString = parts
    .map(
      varInfo =>
        // encodeURIComponent(varInfo.path) + "=" + encodeURIComponent(varInfo.val)
        varInfo.path + "=" + varInfo.val
    )
    .join("&");

  // if (urlEncode) return encodeURI(queryString);
  // else
  // return encodeURI(queryString);
  return queryString;
};

const formData = params => {
  if (_.isEmpty(params)) return null;
  let data = new FormData();

  Object.keys(params).forEach(key => {
    if (!params.hasOwnProperty(key)) return;
    else if (typeof params[key] === "object") append(data, params[key], key);
    else data.append(key, params[key]);
  });

  return data;
};

function append(data, values, name) {
  if (!values && name) data.append(name, "");
  else {
    if (typeof values === "object") {
      Object.keys(values).forEach(k => {
        if (typeof values[k] === "object")
          append(data, values[k], name + "[" + k + "]");
        else data.append(name + "[" + k + "]", values[k]);
      });
    } else data.append(name, values);
  }
}
export default function pause(delay) {
  return new Promise(resolve => {
    setTimeout(_ => {
      resolve()
    }, delay.millis)
  })
}


