import Vue from 'vue';
import Vuex from 'vuex'
import compSettingsPanel from './setting-panel-new';
import compSettingsText from './components/setting-text.vue'
import compSettingsImg from './components/setting-img.vue'
import element from './element';
import fragment from './fragment';
import createGroup from './group';
import VueTheMask from 'vue-the-mask'
import $ from 'jquery'

Vue.use(VueTheMask)

let settingsPanel = new Vue({
  el: '#settings-panel',
  render: h => h(compSettingsPanel)
})
let callBacksDestroyElements = [];
settingsPanel.$children[0].$on('save', async function(){
    for (let index = 0; index < callBacksDestroyElements.length; index++) {
        await callBacksDestroyElements[index]();
    }
    callBacksDestroyElements = [];
    $.ajax({
        url: window.config.link_save,
        type: 'POST',
        data: { ...{
            ajax: 1,
            file: $('html').html()
        }, ...window.config.data_save},
        success(){
            settingsPanel.$children[0].alertSave = 0;
            settingsPanel.$children[0].alertSuccess = 1;
            setTimeout(()=>{
                settingsPanel.$children[0].alertSuccess = 0;
            },2000)
            settingsPanel.$children[0].$emit('initElements');
        }
    })
})
let callBacksInitElements = [];
settingsPanel.$children[0].$on('initElements', async function(){
    for (let index = 0; index < callBacksInitElements.length; index++) {
        await callBacksInitElements[index]();
    }
})
window.settingsPanel = settingsPanel;
async function saveImg(img, callback, type, is_crop){
    $.ajax({
        url: !type ? '/company/upload_logo' : '/company/upload_baner',
        type: 'POST',
        dataType: 'json',
        data: {
            ajax: 1,
            file_data: img,
            is_crop: is_crop ? is_crop : 0
        },  
        success: callback
    })
}
function initImgPanel(elem, wrapper, isInit, isCropper){
    let tippyObject;
    let toolpitContent = '<div id="settingImg"></div>';
    let href;
    let link;
    if($(elem)[0].tagName == 'A' || $(elem).parent("a")[0]){
        link = $(elem)[0].tagName == 'A' ? elem : $(elem).parent("a")[0];
        href = $(link).attr('href');
        toolpitContent = toolpitContent + '<a class="double-link" href="' + href + '">Перейти по ссылке</a>'
        $(link).removeAttr('href');
    }
    let cropper;
    let first_init;
    let dataImg = $(elem).attr('data') ? JSON.parse($(elem).attr('data')) : null;
    if(isCropper){
        $(elem).attr('src', $(elem).attr('origin-src'));
        cropper = initBanner(elem, (event)=>{
            if(!first_init){
                first_init = 1;
                console.log('first_init')
            }else{
                dataImg = Object.assign({}, cropper.getCanvasData());
                window.isChange = 1;
                console.log('change')
            }
        },(event)=>{
            if(dataImg){
                first_init = 0;
                cropper.setCanvasData(dataImg);
            }
        });
    }
    tippy(  wrapper ? wrapper : elem, {
        content: toolpitContent, 
        allowHTML: true,
        trigger: 'mouseenter',
        interactive: true,
        placement: 'bottom',
        onCreate(instance) {
            tippyObject = instance;
            let settingsImg = new Vue({
                el: $(instance.popper).find('#settingImg')[0],
                render: h => h(compSettingsImg)
            })
            settingsImg.$children[0].$on('changeImg', (img)=>{
                window.isChange = 1;
                settingsPanel.$children[0].$data.alertSave = true;
                if(cropper){
                    saveImg(img, (data)=>{
                        cropper.replace(data.json);
                        settingsPanel.$children[0].$data.alertSave = false;
                    }, 1)
                }
                else{
                    saveImg(img, (data)=>{
                        $(elem).attr('src', data.json);
                        settingsPanel.$children[0].$data.alertSave = false;
                    }, 0)
                    
                } 
            });
        },
    });
    
    
    callBacksDestroyElements.push(async ()=>{
        if(cropper){
            //debugger;
            settingsPanel.$children[0].$data.alertSave = true;
            var croppedCanvas;
            var roundedCanvas;
            croppedCanvas = cropper.getCroppedCanvas();
            roundedCanvas = getRoundedCanvas(croppedCanvas);
            let originSrc = $(elem).attr('src');
            cropper.destroy();
            //$(elem).attr('src', roundedCanvas.toDataURL());
            await saveImg(roundedCanvas.toDataURL(), (data)=>{
                $(elem).attr('src', data.json);
            }, 1, 1)
            $(elem).attr('origin-src', originSrc);
            $(elem).attr('data', JSON.stringify(dataImg));
        }
        //debugger;
        if(link)
            $(link).attr('href', href);
        tippyObject.destroy();

    })
    if(!isInit){
        callBacksInitElements.push(()=>{
            initImgPanel(elem, wrapper, 1, isCropper)
        })
    }
}
function getRoundedCanvas(sourceCanvas) {
    var canvas = document.createElement('canvas');
    var context = canvas.getContext('2d');
    var width = sourceCanvas.width;
    var height = sourceCanvas.height;

    canvas.width = width;
    canvas.height = height;
    context.imageSmoothingEnabled = true;
    context.drawImage(sourceCanvas, 0, 0, width, height);
    //context.globalCompositeOperation = 'destination-in';
    context.beginPath();
    context.fill();
    return canvas;
  }
