
<template>
    <div class="setting-text">
        <div class="select-text">
            <div class="select-text-label" @click="()=>{this.isWeight = !this.isWeight}">
                <i class="icon icon-text-bold"></i>
                <span class="select-text-label-text">Толщина<span>
            </div>
            <div class="select-text-values" :class="{ 'select-text-values-active': isWeight}">
                <div class="select-text-value" v-for="item in fontWeight" @click="changeTextWeight(item.value)">{{ item.name }}</div>
            </div>
        </div>
        <div class="select-text">
            <div class="select-text-label" @click="()=>{this.isSize = !this.isSize}">
                <i class="icon icon-font-size"></i>
                <span class="select-text-label-text">Размер<span>
            </div>
            <div class="select-text-values" :class="{ 'select-text-values-active': isSize}" >
                <div class="select-text-value" v-for="item in fontSizes" @click="changeTextSize(item.value)">{{ item.name }}</div>
            </div>
        </div>
        <div class="select-text">
            <div id="colorpickertext" class="select-text-label" @click="()=>{this.isColor = !this.isColor}">
                <i class="icon icon-color-picker"></i>
                <span class="select-text-label-text">Цвет<span>
            </div>
        </div>
    </div>             
</template>
<script>
export default {
    data() {return{
        fontSizes: [
            { value: "10px", name: "10px"},
            { value: "12px", name: "12px"},
            { value: "14px", name: "14px"},
            { value: "16px", name: "16px"},
            { value: "20px", name: "20px"},
            { value: "24px", name: "24px"},
            { value: "28px", name: "28px"},
            { value: "32px", name: "32px"},
            { value: "36px", name: "36px"},
            { value: "40px", name: "40px"}
        ],
        fontWeight: [
            {value: "100", name: "Thin"},
            {value: "300", name: "Light"},
            {value: "400", name: "Normal"},
            {value: "500", name: "Medium"},
            {value: "600", name: "Semi Bold"},
            {value: "700", name: "Bold"}
        ],
        isSize: false,
        isWeight: false,
        isColor: false
    }},
    created(){

        $(document).mouseup((e)=>{ // событие клика по веб-документу
            var div = $(".select-text-label"); // тут указываем ID элемента
            if (!div.is(e.target) // если клик был не по нашему блоку
                && div.has(e.target).length === 0) {
                setTimeout(()=>{
                    this.isSize = false;
                    this.isWeight = false;
                }, 200)
            }
        });
    },
    watch: {
        isSize(){
            if(this.isSize)
                this.isWeight = false;
        },
        isWeight(){
            if(this.isWeight)
                this.isSize = false;
        },
        isColor(){
            if(this.isColor){
                this.isWeight = false;
                this.isSize = false;
            }
        }
    },
    methods: {
        changeTextSize(value){
            this.$emit('changeStyle', {
                'font-size': value
            });
        },
        changeTextWeight(value){
            this.$emit('changeStyle', {
                'font-weight': value
            });
        },
    }
}
</script>

<style>
.setting-text {
    display: flex;
    position: relative;
}
.select-text-label {
    margin: 10px;
    color: white;
    font-size: 18px;
    cursor: pointer;
    text-align: center;
}
.select-text-label i {
    color: white;
}
.select-text-values {
    position: absolute;
    background: white;
    color: black;
    top: 60px;
    border: 1px solid #ccc;
    border-radius: 4px;
    display: none;
}
.select-text-values-active{
    display: block;
}
.select-text-value {
    padding: 10px;
    width: 100px;
    cursor: pointer;
}
.select-text-value:hover {
    background: #ccc;
}
.select-text-label-text {
    display: block;
    font-size: 14px;
}
</style>



