import Vue from 'vue';
import cssGen from 'css-generator';

export default (elem) =>{
    let content = $(elem).html();
    let style = $(elem).attr('styleParams');
    let selector = $(elem).attr('name');
    //$(elem).attr(':style', 'style');
    return new Vue({
        el: elem,
        data: {
            style: style,
            class: selector + '-frag'
        },
        created(){
            $(this.$el).addClass(this.class);
            if(!$('style[name-elem="' + selector + '"]')[0])
                $('main.shop').append('<style name-frag="' + selector + '"></style>')
        },
        watch: {
            
        },
        methods: {
            destroy(){
                $(this.$el).html(content)
                $(this.$el).html(this.content);
            },
            onEditorInput(){
            },
            changeColor(color){
                this.style = { ...this.style, color: color};
            },
            changeStyle(style){
                const css = cssGen.create({
                    indentation: '  ' 
                });
                this.style = { ...this.style,...style};
                css.addRule('.fragment[name="' + selector + '"]', this.style )
                $('style[name-frag="' + selector + '"]').text(css.getOutput())
            },
            callback(func, arg){
                return this[func](arg);
            },
            getStyle(){
                return this.style;
            }
        }
    }) 
}


