
<template>
    <div class="settings-panel-new" id="settings-panel">
        <div class="settings-panel-new-buttons">
            <div class="settings-panel-new-button-wrapper" @mouseover="()=>{this.openGlobalColor = true}" @mouseout="()=>{this.openGlobalColor = false}">
                <div class="settings-panel-new-button">
                    <iconPallete/>
                </div>
                <div class="settings-panel-toolpit" :class="{'settings-panel-toolpit-open': this.openGlobalColor}">
                    <div class="settings-panel-toolpit-title">Основной цвет</div>
                    <div class="color-options">
                        <div v-for="color in defaultColorsGlobal" class="color-options__item" @click="changeColorGlobal(color)" :style="{ 'background': color.hex }" ></div>
                    </div>
                    <colorPicker @change="changeColorGlobal" :color="colorGlobal" />
                    
                    <div class="settings-panel-toolpit-title">Цвет фона</div>
                    <div class="color-options">
                        <div v-for="color in defaultColorsBackground" class="color-options__item" @click="changeColorBackground(color)" :style="{ 'background': color.hex }" ></div>
                    </div>
                    <colorPicker @change="changeColorBackground" :color="colorBackground" />
                    
                    <div class="settings-panel-toolpit-title">Цвет при наведение</div>
                    <div class="color-options">
                        <div v-for="color in defaultColorsHover" class="color-options__item" @click="changeColorHover(color)" :style="{ 'background': color.hex }" ></div>
                    </div>
                    <colorPicker @change="changeColorHover" :color="colorHover" />
                </div>
            </div>
            <div class="settings-panel-new-button-wrapper" @mouseover="()=>{this.openFont = true}" @mouseout="()=>{this.openFont = false}">
                <div class="settings-panel-new-button">
                    <iconFont/>
                </div>
                <div class="settings-panel-toolpit" :class="{'settings-panel-toolpit-open': this.openFont}">
                    <div class="settings-panel-toolpit-title">Шрифт</div>
                    <div class="settings-panel-toolpit-option" @click="()=>{this.globalFont = 'Raleway'}">Raleway</div>
                    <div class="settings-panel-toolpit-option" @click="()=>{this.globalFont = 'Roboto'}">Roboto</div>
                    <div class="settings-panel-toolpit-option" @click="()=>{this.globalFont = 'Montserrat'}">Montserrat</div>
                    <div class="settings-panel-toolpit-option" @click="()=>{this.globalFont = 'Rubik'}">Rubik</div>
                </div>
            </div>
            <div class="settings-panel-new-button-wrapper" @mouseover="()=>{this.openSettings = true}" @mouseout="()=>{this.openSettings = false}">
                <div class="settings-panel-new-button">
                    <iconSetting/>
                </div>
                <div class="settings-panel-toolpit" :class="{'settings-panel-toolpit-open': this.openSettings}">
                    <div class="form-group">
                        <label>Instagram</label>
                        <span class="mask-instagram">instagram.com/</span>
                        <input class="form-control form-control-mask-instagram" v-model="linkInstagram" placeholder="ник" />
                    </div>
                    <div class="form-group">
                        <label>Whatsapp</label>
                        <input  class="form-control" v-model="phoneWhatsapp" placeholder="Телефон" id="WhatsappInput" />
                    </div>
                    <div class="form-group">
                        <label>Адресс</label>
                        <input v-model="geoLocal" @keyup="searchLocal" class="form-control" placeholder="Адресс" />
                        <div class="list-items-geo" v-if="geoLocalItems.length > 0">
                            <div class="list-item-geo" v-for="item in geoLocalItems" @click="setGeoItem(item)" >{{ item.displayName }}</div> 
                        </div>
                        <input :style="{marginTop: '10px'}" v-model="geoLocalСlarification" class="form-control" placeholder="Офис/Квартира" />
                    </div>
                </div>
            </div>
            <div class="settings-panel-new-button-wrapper" @click="save">
                <div class="settings-panel-new-button">
                    <iconSave/>
                </div>
            </div>
        </div>
        <div class="save-preloader" v-if="alertSave">
            <iconPreloader />
        </div>
        <div class="alert alert-success" v-if="alertSuccess" role="alert">
            Изменение сохранено.
        </div>
    </div>
</template>
<script>
import colorPicker from './components/color-picker';
import axios from 'axios';
import iconSave from './icons/save'
import iconPallete from './icons/pallete'
import iconFont from './icons/font'
import iconSetting from './icons/setting'
import iconPreloader from './icons/preloader'
import settingModal from './components/setting-modal';
import { Chrome } from 'vue-color';
import Query from './Query.js'
import cssGen from 'css-generator';
 function getRoundedCanvas(sourceCanvas) {
      var canvas = document.createElement('canvas');
      var context = canvas.getContext('2d');
      var width = sourceCanvas.width;
      var height = sourceCanvas.height;

      canvas.width = width;
      canvas.height = height;
      context.imageSmoothingEnabled = true;
      context.drawImage(sourceCanvas, 0, 0, width, height);
      //context.globalCompositeOperation = 'destination-in';
      context.beginPath();
      context.fill();
      return canvas;
    }
export default {
    components: {
        colorPicker,
        iconSave,
        iconFont,
        iconSetting,
        settingModal,
        Chrome,
        iconPallete,
        iconPreloader
    },
    data() {
        return{
            alertSave: false,
            alertSuccess: false,
            cropper: null,
            openGlobalColor: false,
            openFont: false,
            openSettings: false,
            linkInstagram: '',
            phoneWhatsapp: '',
            instaTokens:{
                I: {
                    pattern: '/^https://(www\.)?instagram\.com/g',
                },
                I2: {
                    pattern: /[0-9a-fA-F]/,
                    transform: v => 'n/'+ v
                },
                T: {
                    pattern: /[0-9a-fA-F]+/
                }
            },
            defaultColorsGlobal: [
                { hex: '#05277c' },
                { hex: '#0065b3' },
                { hex: '#009cdb' },
                { hex: '#a0d5e5' },
                { hex: '#00573d' },
                // { hex: '#f7ce00' },
                // { hex: '#fef800' },
                { hex: '#c06720' },
                { hex: '#ffa016' },
                // { hex: '#ff6600' },
                { hex: '#9385c6' },
                // { hex: '#a048ad' },
                { hex: '#e398c5' },
                { hex: '#ff1466' },
                // { hex: '#ff048c' },
                // { hex: '#c6085b' },
                { hex: '#ff0102' },
                // { hex: '#6bc941' },
                // { hex: '#000000' },
                { hex: '#626262' },
                { hex: '#a2a2a2' },
            ],
            colorGlobal: { hex: '#26cae5' },
            defaultColorsHover: [
                { hex: '#05277c' },
                { hex: '#0065b3' },
                { hex: '#009cdb' },
                { hex: '#a0d5e5' },
                { hex: '#00573d' },
                // { hex: '#f7ce00' },
                // { hex: '#fef800' },
                { hex: '#c06720' },
                { hex: '#ffa016' },
                // { hex: '#ff6600' },
                { hex: '#9385c6' },
                // { hex: '#a048ad' },
                { hex: '#e398c5' },
                { hex: '#ff1466' },
                // { hex: '#ff048c' },
                // { hex: '#c6085b' },
                { hex: '#ff0102' },
                // { hex: '#6bc941' },
                // { hex: '#000000' },
                { hex: '#626262' },
                { hex: '#a2a2a2' },
            ],
            colorHover: { hex: '#26cae5' },
            defaultColorsBackground: [
                { hex: '#f2f2f2' },
                { hex: '#f0e8dc' },
                { hex: '#f0dcdc' },
                { hex: '#eef0dc' },
                { hex: '#dcf0e3' },
                { hex: '#dcdcf0' },
            ],
            colorBackground: { hex: '#f2f2f2' },

            activeElem: null,
            openText: false,
            openGlobal: false,
            openSetting: false,
            openBannerSetting: false,

            textBanner: '',
            textBannerFontFamily: '',
            textBannerFontSize: '',
            textBannerFontWeight: '',
            colorTextBanner: {},
            positionBannerImg: 0,
            maxTopBannerImg: 0,

            textFontFamily: '',
            textFontSize: '',
            textFontWeight: '',
            colorText: {},

            geoLocal: '',
            geoLocalItems: [],
            geoLocalСlarification: '',

            globalFont: 'Raleway',

            fontSizes: [
                { value: "10px", name: "10px"},
                { value: "12px", name: "12px"},
                { value: "14px", name: "14px"},
                { value: "16px", name: "16px"},
                { value: "20px", name: "20px"},
                { value: "24px", name: "24px"},
                { value: "28px", name: "28px"},
                { value: "32px", name: "32px"},
                { value: "36px", name: "36px"},
                { value: "40px", name: "40px"}
            ],
            fontWeight: [
                {value: "100", name: "Thin"},
                {value: "300", name: "Light"},
                {value: "400", name: "Normal"},
                {value: "500", name: "Medium"},
                {value: "600", name: "Semi Bold"},
                {value: "700", name: "Bold"}
            ],
            dataBanner: null
        }
    },
    created(){;
        $('main.shop').append('<style name-elem="global-style-font"></style>')
        $('main.shop').append('<style name-elem="global-style-color"></style>')
        $('main.shop').append('<style name-elem="global-style-color-hover"></style>')
        //$('.banner-img>img').click(()=>{
            
                //this.initBanner();      
        //})
        let heigth = $('.banner-img img').css('heigth');
        this.maxTopBannerImg = heigth - 300;
        
    },
    watch: {
        globalFont(){
            window.isChange = 1;
            console.log('change')
            const css = cssGen.create({
                    indentation: '  ' 
            });
            css.addRaw('.element, .fragment { font-family: ' + this.globalFont + ', sans-serif }');
            $('style[name-elem="global-style-font"]').text(css.getOutput())
        },
        positionBannerImg(){
            $('.banner-img img').css('top', -this.positionBannerImg);
        },
        linkInstagram(){
            window.isChange = 1;
            console.log('change')
            $('#instagram-link').attr('href', 'https://www.instagram.com/' + this.linkInstagram);
        },
        phoneWhatsapp(){
            window.isChange = 1;
            console.log('change')
            $('#whatsapp-link').attr('href', 'https://wa.me/' + this.phoneWhatsapp);
        },
        
        textFontFamily(){
            this.activeElem.changeStyle({
                'font-family': this.textFontFamily
            })
        },
        textFontSize(){
            this.activeElem.changeStyle({
                'font-size': this.textFontSize
            })
        },
        textFontWeight(){
            this.activeElem.changeStyle({
                'font-weight': this.textFontWeight
            })
        },
        textBannerFontFamily(){
            $('.banner-title').css({
                'font-family': this.textBannerFontFamily
            })
        },
        textBannerFontSize(){
            $('.banner-title').css({
                'font-size': this.textBannerFontSize
            })
        },
        textBannerFontWeight(){
            $('.banner-title').css({
                'font-weight': this.textBannerFontWeight
            })
        },
        positionImgBanner(){
            $('.')
        },
        activeElem(){
            // if(this.cropper)
            //     this.saveBanner()
            if(this.activeElem != null){
                this.textFontSize = this.activeElem.$data.style['font-size'];
                this.textFontWeight = this.activeElem.$data.style['font-weight'];
                this.colorText = this.activeElem.$data.style['color'];
                this.openText = true;
                this.openGlobal = false;
                this.openSetting = false;
                this.openBannerSetting = false;
                let cntx = this;
                $("#colorpickertext").spectrum({
                    color: this.colorText ? this.colorText.hex : null ,
                    move: function(color){
                        cntx.changeColorText(color)    
                        console.log(color);                
                    },
                });
            }
            else{
                this.openText = false;
            } 
        },
        colorGlobal(){
            window.isChange = 1;
            console.log('change')
            const css = cssGen.create({
                    indentation: '  ' 
            });
            css.addRaw('.icon-setting, .card-price, .link { color: ' + this.colorGlobal.hex + '!important }');
            css.addRaw('.border-setting { border-color: ' + this.colorGlobal.hex + '!important }');
            css.addRaw('.background-setting { background: ' + this.colorGlobal.hex + '!important }')
            $('style[name-elem="global-style-color"]').text(css.getOutput())
        },
        colorHover(){
            window.isChange = 1;
            console.log('change')
            const css = cssGen.create({
                    indentation: '  ' 
            });
            css.addRaw('a:hover { color: ' + this.colorHover.hex + '!important }');
            $('style[name-elem="global-style-color-hover"]').text(css.getOutput());

        },
        colorBackground(){
            window.isChange = 1;
            console.log('change')
            $('.shop').css('background', this.colorBackground.hex);
        },
        geoLocalСlarification(){
            window.isChange = 1;
            console.log('change')
            $('#adress').text(this.geoLocal + ', ' + this.geoLocalСlarification);
        },
        geoLocal(){
            window.isChange = 1;
            console.log('change')
            $('#adress').text(this.geoLocal + ', ' + this.geoLocalСlarification);
        }
    },
    computed:{
    },
    methods: {
        changeOriginBannerImg(img){
            this.originBannerImg = img;
        },
        initBanner(){
            
            let data;
            let cntx = this;
            $('.banner-img').attr('style', '');
            $('.banner-img>img').addClass('cropper-hidden');
            this.openBannerSetting = true;
            if(this.cropper){
                $('.cropper-container').css('display', 'block');
                return;
            }
            if(!this.originBannerImg){
                this.originBannerImg = $('.banner-img>img').attr('src');
            }
            else{
                $('.banner-img>img').attr('src', this.originBannerImg); 
            }
            this.cropper = new Cropper($('.banner-img>img')[0], {
                dragMode: 'move',
                autoCropArea: 1,
                ready(){
                    console.log(cntx.dataBanner)
                    if(cntx.dataBanner){
                        cntx.cropper.setCanvasData(cntx.dataBanner);
                    }
                        
                },
                restore: false,
                modal: false,
                guides: false,
                highlight: false,
                cropBoxMovable: false,
                cropBoxResizable: false,
                toggleDragModeOnDblclick: false,
                viewMode: 3,
                crop(event) {
                    if(!this.cropper.first_init){
                        this.cropper.first_init = 1;
                    }else{
                        cntx.dataBanner = Object.assign({}, cntx.cropper.getCanvasData());
                        window.isChange = 1;
                        console.log('change')
                    }
                    cntx.stylePositionBannerWrapper = $('.cropper-canvas').attr('style');
                    cntx.stylePositionBannerImg = $('.cropper-canvas img').attr('style');
                }
            });
            window.cropper = this.cropper;
        },
        changeTextSize(value){
            this.textFontSize = value;
        },
        changeTextWeight(value){
            this.textFontWeight = value;
        },
        searchLocal(){
            ymaps.suggest('Казахстан, ' + this.geoLocal).then((items) => {
                this.geoLocalItems = items;
            });
        },
        saveBanner(){
            // if(this.stylePositionBannerWrapper){
            //     $('.banner-img').attr('style', this.stylePositionBannerWrapper);
            //     $('.cropper-container').css('display', 'none');
            //     $('.banner-img>img').removeClass('cropper-hidden');
            // }
            var croppedCanvas;
            var roundedCanvas;
            var roundedImage;
            croppedCanvas = this.cropper.getCroppedCanvas();
            roundedCanvas = getRoundedCanvas(croppedCanvas);
            this.cropper.destroy();
            this.cropper = null;
            $('.banner-img>img').attr('src', roundedCanvas.toDataURL());
            $('.cropper-container').css('display', 'none');
            $('.banner-img>img').removeClass('cropper-hidden');
            this.openBannerSetting = false;
        },
        setGeoItem(item){
            this.geoLocal = item.displayName;
            this.isSelectLocal = 1;
            this.geoLocalItems = [];
        },
        changeColorTextBanner(color){
            this.colorTextBanner = color;
            $('.banner-title').css('color', color.hex);
        },
        changeColorText(color){
            this.colorText = color;
            this.activeElem.changeStyle({ color: color });
        },
        closeTextSettings(){
            this.activeElem.changeStyle({
                outline: '0px'
            })
            console.log(this.activeElem = style);
            this.openText = false;
        },
        closeSettings(){
            this.openSetting = false;
        },
        closeGlobalSettings(){
            this.openGlobal = false;
        },
        changeColorGlobal(color){
            window.isChange = 1;
            console.log('change')
            this.colorGlobal = color;
        },
        changeColorHover(color){
            window.isChange = 1;
            console.log('change')
            this.colorHover = color;
        },
        changeColorBackground(color){
            window.isChange = 1;
            console.log('change')
            this.colorBackground = color;
        },
        openGlobalStyle(){
            this.activeElem = null;
            if(this.openGlobal){
                this.openGlobal = false;
            }else{
                this.openGlobal = true;
                this.openSetting = false;
                this.openText = false;
                this.openBannerSetting = false;
            }
            
        },
        openSettings(){
            this.activeElem = null;
            if(this.openSetting){
                this.openSetting = false;
            }else{
                this.openSetting = true;
                this.openText = false;
                this.openGlobal = false;
                this.openBannerSetting = false;
            }
            
        },
        changeBanner(){
            let file = this.$refs.fileBanner.files;
            if (!file.length)
                return;
            this.createImage(file[0], (e) => {
                $('.banner-img img').attr('src', e.target.result )
            });
        },
        changeLogo(){
            let file = this.$refs.fileLogo.files;
            if (!file.length)
                return;
            this.createImage(file[0], (e) => {
                $('.logo').attr('src', e.target.result )
            });
        },
        createImage: function(file, callback) {
            let reader = new FileReader();
            reader.onload = callback;
            reader.readAsDataURL(file);
        },
        save(){
            this.alertSave = true;
            window.isChange = 0;
            this.$emit('save');
            //this.saveBanner();
            //let cntx = this;
            // $.ajax({
            //     url: window.config.link_save,
            //     type: 'POST',
            //     data: { ...{
            //         ajax: 1,
            //         file: $('html').html()
            //     }, ...window.config.data_save},
            //     success(){
            //         this.alertSave = 0;
            //         this.alertSuccess = 1;
            //         setTimeout(()=>{
            //             this.alertSuccess = 0;
            //         },2000)
            //         cntx.$emit('initElements');
            //     }
            // })
            // setTimeout(()=>{
            //     this.alertSave = false;
            //     console.log(this.alertSave);
            //     this.alertSuccess = true;
            //     this.initBanner();
            //     setTimeout(()=>{
            //         this.alertSuccess = false;
            //         console.log(this.alertSuccess);
            //     },20000)
            //     cntx.$emit('initElements');
            // },1000)
        }
    }
}
</script>

<style>

div.settings-panel-new {
    position: fixed;
    top: 0px;
    bottom: 0px;
    margin: auto;
    max-height: 100%;
    z-index: 1000;
}
.settings-panel-new-buttons {
    position: absolute;
    top: 0;
    bottom: 0px;
    margin: auto;
}
.settings-panel-new-button-wrapper{
    position: relative;
}
.settings-panel-new-button {
    cursor: pointer;
    padding: 15px;
    position: relative;
    margin: 10px;
    background: white;
    border-radius: 5px;
    border: 1px solid #ccc;
}
.settings-panel-new-button-wrapper:hover .settings-panel-new-button {
    border: 1px solid #26cae5;
}
.settings-panel-new-button svg {
    width: 30px;
    height: 30px;
    fill: #5b5b5b;
}
.settings-panel-toolpit {
    position: absolute;
    width: 250px;
    background: #fff;
    left: 76px;
    top: -1px;
    padding: 12px;
    border-radius: 5px;
    border: 1px solid #ccc;
    display: none;
}
.form-group {
    position: relative;
}
.mask-instagram {
    position: absolute;
    top: 35px;
    left: 13px;
    color: #495057;
}
.settings-panel .form-control{
    height: 30px;
    line-height: 30px;
}
.form-control-mask-instagram {
    padding-left: 122px;
    height: 30px;
    line-height: 30px;
}
.settings-panel-toolpit-open{
    display: block;
}
.color-options {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 10px;
}
.color-pick {
    width: 100%;
    height: 35px;
    background-color: #333;
    cursor: pointer;
    transition: .2s ease;
    border: 2px solid #5f5f5f;
    border-radius: 29px;
}
.settings-panel-toolpit-option {
    background: white;
    padding: 5px 15px;
    border: 1px solid #ebebeb;
    margin-bottom: 5px;
    border-radius: 4px;
    cursor: pointer;
}
.settings-panel-toolpit-option:hover {
    background: #26cae536;
}
.mask-instagram {
    position: absolute;
    top: 35px;
    left: 13px;
    color: #495057;
}
.form-control-mask-instagram {
    padding-left: 122px;
}
.settings-panel .form-group{
    position: relative;
}
.list-items-geo {
    border: 1px solid #ccc;
    margin: auto;
    border-top: 0px;
    margin-top: -12px;
    padding-top: 12px;
}
.list-item-geo {
    margin: auto;
    border: 1px solid #ccc;
    background: #f4f4f4;
    padding: 5px 15px;
    color: black;
    margin-bottom: 10px;
    margin-top: 10px;
    border-radius: 10px;
    cursor: pointer;
    width: 95%;
    font-size: 14px;
}
.alert {
    position: fixed;
    left: 11px;
    bottom: 10px;
    width: 300px;
    margin: auto;
    text-align: center;
}
.save-preloader {
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 100000;
    background: #00000045;
    padding-top: 18%;
    text-align: center;
}
</style>



