import Vue from 'vue';
import CKEditor from '@ckeditor/ckeditor5-vue';
import cssGen from 'css-generator';

Vue.use( CKEditor );
import BalloonEditor from '@ckeditor/ckeditor5-build-balloon';
export default (elem) =>{
    let content = $(elem).html();
    let style = $(elem).attr('styleParams');
    let selector = $(elem).attr('name');
    //$(elem).attr(':style', 'style');
    $(elem).html('<ckeditor @ready="ReadyEditor" @input="onEditorInput" :editor="editor" v-model="editorData"></ckeditor>')
    return new Vue({
        el: elem,
        data: {
            style: style,
            editor: BalloonEditor,
            editorData: content,
            class: selector
        },
        created(){
            if(!$('style[name-elem="' + selector + '"]')[0])
                $('main.shop').append('<style name-elem="' + selector + '"></style>')
        },
        watch: {
            
        },
        methods: {
            destroy(){
                console.log(this.editorData);
                try{
                    $(this.$el).html($(this.editorData).text())
                    if(!$(this.editorData).text()){
                        $(this.$el).html(this.editorData);
                    }
                }catch(err){
                    $(this.$el).html(this.editorData)
                }
                this.$destroy();
            },
            init(){
                $(this.$el).html('<ckeditor @ready="ReadyEditor" @input="onEditorInput" :editor="editor" v-model="editorData"></ckeditor>')
            },
            ReadyEditor(){
            },
            onEditorInput(){
                console.log('change');
                window.isChange = 1;
            },
            changeColor(color){
                this.style = { ...this.style, color: color};
            },
            changeStyle(style){
                const css = cssGen.create({
                    indentation: '  ' 
                });
                this.style = { ...this.style,...style};
                console.log(this.style, style);
                let newStyle = {};
                for(let key in this.style){
                    newStyle[key] = this.style[key] + '!important';
                }
                css.addRule('.element[name="' + selector + '"]', newStyle )
                $('style[name-elem="' + selector + '"]').text(css.getOutput())
            },
            callback(func, arg){
                return this[func](arg);
            },
            getStyle(){
                return this.style;
            }
        }
    })
}


