
<template>
    <div class="setting-img">
        <div class="setting-img-form">
                <label class="setting-img-form-text">Загрузить изображение</label>
                <i class="icon icon-arrow"></i>
                <input v-on:change="createImage" ref="file" class="setting-img-form-input" type="file">
        </div>
    </div>             
</template>
<script>
export default {
    data() {return{
        file: ''
    }},
    methods: {
        createImage: function() {
            let file = this.$refs.file.files;
            if (!file.length)
                return;
            let reader = new FileReader();
            reader.onload = (e) => {
                this.$emit('changeImg', e.target.result);
            };
            reader.readAsDataURL(file[0]);
        },
    }
}
</script>

<style>
    .setting-img-form-input {
        position: absolute;
        left: 0;
        top: -50%;
        height: 150%;
        width: 100%;
        cursor: pointer;
        cursor: pointer;
        z-index: 10000;
    }
    .setting-img-form-text{
        display:block;
    }
    .setting-img {
        padding: 4px 15px;
        margin: 4px 0px;
        cursor: pointer;
        border: 1px dashed #fff;
        position: relative;
        overflow: hidden;
    }
    .setting-img-form {
        text-align: center;
    }
</style>



